#cave {
	position: relative;
	.container {
		display: grid;
		place-items: center;
		grid-template-columns: repeat(4, 1fr);
		grid-template-areas: ". thank_you thank_you .";
		grid-row-gap: 50px;

		.thank_you {
			z-index: 100;
			grid-area: thank_you;
			margin-top: -50vh;

			p {
				margin-bottom: 1.5em;
			}
		}
	}

	.cave_top {
		margin-top: -0.5em;
		width: 100%;
		height: auto;
		top: 0;
		z-index: 200;
	}
	.cave_bottom_med {
		z-index: -100;
		margin-top: -35vh;
		width: 100%;
		height: auto;
		bottom: 0;
	}

	.cave_bottom_mobile,
	.cave-bird,
	.fuzzy,
	.lizard {
		display: none;
	}
}

@media (min-width: 2000px) {
	#cave {
		.container {
			display: grid;
			place-items: center;
			grid-template-columns: repeat(4, 1fr);
			grid-template-areas: ". thank_you thank_you .";
			grid-row-gap: 50px;

			.thank_you {
				text-align: center;
				z-index: 100;
				grid-area: thank_you;
				margin-top: -40em;

				p {
					text-align: center;
				}

				.fuzzy {
					margin-top: -10vh;
					margin-bottom: -2em;
					z-index: 100;
					display: inline-block;
					left: 0;
					width: 25vh;
				}

				.lizard {
					display: inline-block;
					width: 35vh;
					left: 50%;
					transform: translateX(-50%);
					z-index: 200;
					bottom: 4vh;
					position: absolute;
				}
			}
		}

		.cave_bottom_mobile {
			display: inline-block;
			padding-top: 0vh;
			margin-top: -80vh;
			z-index: -100%;
			width: 100%;
			height: auto;
		}

		.cave_bottom_med {
			display: none;
		}
	}
}

@media (min-width: $breakpoint-large) {
	.cave_bottom_mobile,
	.cave-bird,
	.fuzzy,
	.lizard {
		display: none;
	}

	.thank_you {
		z-index: 100;
		margin-top: -50vh;
	}
}

@media (min-width: $breakpoint-medium) {
	.cave_bottom_med {
		margin-top: -100vh;
	}

	.cave_bottom_mobile,
	.cave-bird,
	.fuzzy,
	.lizard {
		display: none;
	}

	.thank_you {
		z-index: 100;
		grid-area: thank_you;
		margin-top: -120vh;
	}
}

@media (max-width: 1050px) {
	#cave {
		.container {
			display: grid;
			place-items: center;
			grid-template-columns: repeat(4, 1fr);
			grid-template-areas: "thank_you thank_you thank_you thank_you";
			grid-row-gap: 50px;

			.thank_you {
				text-align: center;
				z-index: 100;
				grid-area: thank_you;
				margin-top: -3em;

				p {
					text-align: center;
				}

				.fuzzy {
					margin-top: -10vh;
					margin-bottom: -2em;
					z-index: 100;
					display: inline-block;
					left: 0;
					width: 25vh;
				}

				.lizard {
					display: inline-block;
					width: 35vh;
					left: 50%;
					transform: translateX(-50%);
					z-index: 200;
					bottom: 4vh;
					position: absolute;
				}
			}
		}

		.cave_bottom_mobile {
			display: inline-block;
			padding-top: 0vh;
			margin-top: -10vh;
			z-index: -100%;
			width: 100%;
			height: auto;
		}

		.cave_bottom_med {
			display: none;
		}
	}
}

@media (max-width: $breakpoint-small) {
	.thank_you {
		.fuzzy {
			visibility: visible;
			padding-bottom: 2em;
		}

		img {
			max-width: 35%;
		}
	}
}

@media (max-width: 400px) {
	.illustrators {
		text-align: center;
		max-width: 220px;
		margin: 0 auto;
		margin-top: 1em;
	}
}
